import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection } from 'angularfire2/firestore';
import { Observable } from 'rxjs';
import { map } from 'rxjs/internal/operators/map';
import { functionality } from 'src/Table/table';

@Injectable({
  providedIn: 'root'
})
export class FunctionalityService {

  private functionalityCollectionList: AngularFirestoreCollection<any>;
  private functionlityList: Observable<functionality[]>;
  //db: any;
  constructor(private db: AngularFirestore) { 
    this.functionalityCollectionList = db.collection<functionality>('functionality');
   }
   
   create(functionality) { 
    return this.functionalityCollectionList.add(functionality);
  }
  getAllFunctionality() {
    this.functionlityList = this.functionalityCollectionList.snapshotChanges().pipe(
      map(actions => {
        return actions.map(a => {
          const data = a.payload.doc.data();
          const id = a.payload.doc.id;
          return { id, ...data };
        });
      })
    );
    return this.functionlityList;
  }
updateFunctionality(funObj:functionality,id:string){
  return this.functionalityCollectionList.doc(id).update(funObj);
}
removeFunctionality(id:string){
  return this.functionalityCollectionList.doc(id).delete()
}
}
