import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AlertController, Platform } from '@ionic/angular';
import { Account, functionality } from 'src/Table/table';
import { AccountService } from '../Service/account.service';
import { AuthService } from '../Service/auth.service';
import { FunctionalityService } from '../Service/Functionality.service';
import { UserRoleService } from '../Service/userRole.service';
import { Geolocation } from '@ionic-native/geolocation/ngx';
import { NativeGeocoder, NativeGeocoderOptions, NativeGeocoderResult } from '@ionic-native/native-geocoder/ngx';
import { SharedService } from '../Service/shared.service';
@Component({
  selector: 'app-menu',
  templateUrl: './menu.page.html',
  styleUrls: ['./menu.page.scss'],
})
export class MenuPage implements OnInit {
  public selectedIndex = 0;
  roleType: string;
  listOfFunctionality: functionality[];
  listOfUser: Account[];
  userName: any;
  transcation: boolean;
  ListOfMenu1: any[] = [];
  pageList: any[] = [];
  usePicker: boolean;
  latitude: any = 0; //latitude
  longitude: any = 0; //longitude
  address: string;
  listOfAccount: Account[];
  base64textString: string;
  constructor(private authServices: AuthService,
    private router: Router,
    private alertCtrl: AlertController,
    private functionalityService: FunctionalityService,
    private userRoleService: UserRoleService,
    private accountService: AccountService,
    private platform: Platform,
    private geolocation: Geolocation,
    private nativeGeocoder: NativeGeocoder) { }

  ngOnInit() {
    if ((this.platform.is('mobile') && !this.platform.is('hybrid')) ||
      this.platform.is('desktop')
    ) {
      this.usePicker = true;
    }
    const path = window.location.pathname.split('folder/')[1];
    if (path !== undefined) {
      // this.selectedIndex = this.appPages.findIndex(page => page.title.toLowerCase() === path.toLowerCase());
    }
    this.getRoute();
    this.getCurrentCoordinates();
    this.getAccount();
  }
  getAccount() {
    this.accountService.getAllAccount().subscribe(res => {
      this.listOfAccount = res;
      this.base64textString = res.find(c => c.id == localStorage.getItem("userId")).photo;
    })
  }
  getCurrentCoordinates() {
    this.geolocation.getCurrentPosition().then((resp) => {
      this.latitude = resp.coords.latitude;
      this.longitude = resp.coords.longitude;
      let data ={
        lat:this.latitude,
        lon:this.longitude
      }
     // this.getAddress(this.latitude,this.longitude);
      console.log(resp.coords);
     }).catch((error) => {
       console.log('Error getting location', error);
     });
  }
    // geocoder options
    nativeGeocoderOptions: NativeGeocoderOptions = {
      useLocale: true,
      maxResults: 5
    };
    // get address using coordinates
    getAddress(lat,long){
      this.nativeGeocoder.reverseGeocode(lat, long, this.nativeGeocoderOptions)
      .then((res: NativeGeocoderResult[]) => {
        this.address = this.pretifyAddress(res[0]);
        console.log(this.address)
      })
      .catch((error: any) => {
        alert('Error getting location'+ JSON.stringify(error));
      });
    }
  
    // address
    pretifyAddress(address){
      let obj = [];
      let data = "";
      for (let key in address) {
        obj.push(address[key]);
      }
      obj.reverse();
      for (let val in obj) {
        if(obj[val].length)
        data += obj[val]+', ';
      }
      return address.slice(0, -2);
    }

  ionViewWillEnter() {
    this.ListOfMenu1 = [
      { title: 'customer', url: '/signup-customer', icon: 'person-add' },
      { title: 'order', url: '/order', icon: 'cart' },
      { title: 'location', url: '/location', icon: 'locate' },
      { title: 'home', url: '/home', icon: 'home' },
      { title: 'orders', url: '/restaurant-home', icon: 'cart' },
      { title:'Profile',url:'/profile',icon:'person'}
    ]; 
  }

  getRoute() {
    this.functionalityService.getAllFunctionality().subscribe(result => {
      this.listOfFunctionality = result;
      if (result.length > 0) {
        this.roleType = localStorage.getItem("roleType");
        this.userName = localStorage.getItem("fullName");
        this.userRoleService.getUerRoleId( this.roleType).subscribe(res => {
            let result = res.filter(c => c.userId ==  this.roleType);
            let active = localStorage.getItem("active");
            if (result.length > 0 && active =="true") {
              //To check transcation menu in user role
              result.forEach(element => {
                let isFound = this.listOfFunctionality.filter(c => c.SN == +element.funId)
                if (isFound.length > 0) {
                  let rol = this.ListOfMenu1.filter(c => c.title == isFound[0].compName)[0];
                  if (rol) {
                    let y = {
                      title: isFound[0].compName,
                      url: rol.url,
                      icon: rol.icon
                    }
                    this.pageList.push(y);
                    }
                }
              });
            }
          })
      
      }
    })
  }
 
  readPath1(path) {
    let pa = path.url
    this.router.navigate(["/menu/" + pa]);
  }
  async AlertInternet() {
    const alert = await this.alertCtrl.create({
      cssClass: 'my-custom-class',
      header: 'Internet',
      // subHeader: 'Subtitle',
      message: 'Please turn on wifi or data',
      buttons: ['OK']
    });

    await alert.present();
  }
  async logout(): Promise<void> {
    this.authServices.logOutUser().
      then(
        () => {
          localStorage.setItem("userId", null);
          localStorage.setItem("fullName",null);
          localStorage.setItem("active",null);
          localStorage.setItem("roleType",null);
          this.router.navigateByUrl('login');
        },
        async error => {
          const alert = await this.alertCtrl.create({
            message: error.message,
            buttons: [{ text: 'ok', role: 'cancel' }],
          });
          await alert.present();
        }
      );
  }
}
